`, "",{"version":3,"sources":["webpack://./src/components/checkBox/checkbox-group.vue"],"names":[],"mappings":";AAqEA;EACE,aAAa;EACb,sBAAsB;AACxB","sourcesContent":["<template>\n  <div\n    :class=\"[\n      'dpzvc3-checkBoxGroup',\n      vertical ? 'dpzvc3-checkBoxGroup-vertical' : ''\n    ]\"\n  >\n    <slot />\n  </div>\n</template>\n\n<script>\nimport { defineComponent, ref, watch, provide } from 'vue'\n// import Emitter from '../../mixin/emitter'\n\nexport default defineComponent({\n  name: 'CheckBoxGroup',\n  // mixins: [Emitter],\n\n  props: {\n    modelValue: {\n      type: Array,\n      default: () => []\n    },\n    single: {\n      type: Boolean,\n      default: false\n    },\n    vertical: {\n      type: Boolean,\n      default: false\n    }\n  },\n\n  emits: ['update:modelValue', 'on-change'],\n\n  setup (props, { emit }) {\n    // alert(props)\n    const currentValue = ref([...props.modelValue])\n\n    /**  checkbox  */\n    const toggleOption = (label) => {\n      let newValue = []\n      newValue = [...label]\n      // currentValue.value = newValue\n      emit('update:modelValue', newValue)\n      emit('on-change', newValue)\n    }\n\n    /**  checkbox */\n    provide('CheckBoxGroup', {\n      currentValue,\n      toggleOption\n      // single: props.single\n    })\n\n    watch(\n      () => props.modelValue,\n      (val) => {\n        currentValue.value = [...val]\n      }\n    )\n\n    return {}\n  }\n})\n</script>\n\n<style scoped>\n.dpzvc3-checkBoxGroup-vertical {\n  display: flex;\n  flex-direction: column;\n}\n</style>\n"],"sourceRoot":""}]);
