<template>
  <div>
    <Popup
      v-model="visible"
      position="bottom"
      :styles="{ background: 'transparent' }"
    >
      <div :class="classes">
        <ul :class="wrapperClasses">
          <li
            v-for="(item, key) in actions"
            :key="key"
            :class="wrapperActionClass"
            @click="emit(item, key)"
          >
            <span>{{ item.text }}</span>
          </li>
        </ul>
        <a
          v-if="cancleText"
          href="javascript:;"
          :class="cancleClass"
          @click="visible = false"
        >{{ cancleText }}</a>
      </div>
    </Popup>
  </div>
</template>

<script>
import { ref, computed, watch } from 'vue'
import Popup from '../popup'

const prefixCls = 'dpzvc3-actionSheet'

export default {
  name: 'ActionSheet',
  components: { Popup },
  props: {
    modelValue: { // Vue 3 v-model  modelValue
      type: Boolean,
      default: false
    },
    items: {
      type: Array,
      default: () => []
    },
    cancleText: {
      type: String,
      default: ''
    }
  },
  setup (props, { emit }) {
    // 
    const visible = ref(props.modelValue)
    const actions = ref(props.items)

    //  props.modelValue
    watch(() => props.modelValue, (val) => {
      visible.value = val
    })

    //  visible 
    watch(visible, (val) => {
      emit('update:modelValue', val)
    })

    // 
    const classes = computed(() => [prefixCls])
    const wrapperClasses = computed(() => [`${prefixCls}-wrapper`])
    const wrapperActionClass = computed(() => [`${prefixCls}-wrapper-action`])
    const cancleClass = computed(() => [`${prefixCls}-cancle`])

    // 
    const emitAction = (item, index) => {
      if (item.onClick && typeof item.onClick === 'function') {
        item.onClick(item, index)
      }
      visible.value = false
    }

    return {
      visible,
      actions,
      classes,
      wrapperClasses,
      wrapperActionClass,
      cancleClass,
      emit: emitAction
    }
  }
}
</script>
