<template>
  <div :class="classes">
    <input
      :id="name"
      v-model="currentValue"
      type="checkbox"
      :class="inputCheckClasses"
    >
    <label
      :for="name"
      :class="sizeClasses"
    />
  </div>
</template>

<script>
import { defineComponent, ref, computed, watch, onMounted } from 'vue'
import { randomStr } from '../../utils/util'

const prefixCls = 'dpzvc3-switch'

export default defineComponent({
  name: 'SwitchBar',
  props: {
    id: {
      type: String,
      default: null
    },
    modelValue: { // Vue 3  v-model  modelValue
      type: Boolean,
      default: false
    },
    size: {
      type: String,
      default: 'small',
      validator (val) {
        return ['small', 'large'].includes(val)
      }
    }
  },
  emits: ['update:modelValue'],
  setup (props, { emit }) {
    const name = ref(props.id || '')
    const currentValue = ref(props.modelValue)

    //  v-model 
    watch(() => props.modelValue, val => {
      currentValue.value = val
    })

    //  value 
    watch(currentValue, val => {
      emit('update:modelValue', val)
    })

    //  id
    onMounted(() => {
      if (!props.id) {
        name.value = Date.now() + '_' + randomStr()
      }
    })

    const classes = computed(() => [prefixCls])
    const inputCheckClasses = computed(() => [`${prefixCls}-checkbox`, 'hidden'])
    const sizeClasses = computed(() => [
      `${prefixCls}-ui`,
      { small: props.size === 'small' }
    ])

    return {
      name,
      currentValue,
      classes,
      inputCheckClasses,
      sizeClasses
    }
  }
})
</script>
