/**
 * Vue 3 prompt confirm
 *  createVNode + render
 */

import { createVNode, render } from 'vue'
import Prompt from './prompt.vue'

// let seed = 0

Prompt.newInstance = (properties = {}) => {
  // const id = `dpzvc3-prompt-${seed++}`
  const instance = null
  // 
  const container = document.createElement('div')
  // container.id = id
  document.body.appendChild(container)
  //  props
  const props = {
    value: false,
    text: '',
    placeholderText: '',
    width: '70%',
    title: '',
    okText: '',
    cancleText: '',
    loading: false,
    showCancle: true,
    spec: '',
    message: '',
    validator: null,
    onOk: () => {},
    onCancle: () => {},
    ...properties
  }

  const updateVNode = () => {
    // vnode
    // console.log(props, 'props')
    const vnode = createVNode(Prompt, {
      ...props,
      //  update:value 
      'onUpdate:value': (val) => {
        //  emit('update:value', false) 
        if (val === false) {
          vnode.component.props.value = false // 
          destroy() //  DOM
        }
      },
      onRemove: destroy
    })
    // 
    render(vnode, container)
    // instance = vnode.component
  }
  // 
  const destroy = () => {
    props.value = false
    setTimeout(() => {
      render(null, container)
    }, 300)
  }

  return {
    /**
     * 
     */
    show (options = {}) {
      // alert(1)
      Object.keys(options).forEach(key => {
        props[key] = options[key]
      })
      // console.log(instance.props, 'props')
      props.value = true
      updateVNode()
    },

    /**
     * 
     */
    remove () {
      props.value = false

      destroy()
    },

    component: instance
  }
}

export default Prompt
